import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { AppVersion } from '@ionic-native/app-version';

@Component({
  selector: 'support-and-feedback',
  templateUrl: 'supportandfeedback.html'
})
export class SupportAndFeedbackPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private appVersion: AppVersion) {
      alert(appVersion.getAppName() + ' - ' + appVersion.getVersionNumber());
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad - SupportAndFeedbackPage');
  }

  closeWindow() {
    this.navCtrl.pop();
  }

}



